    -moz-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out; }
    .navigation__toggle span:nth-child(1) {
      top: 0px; }
    .navigation__toggle span:nth-child(2), .navigation__toggle span:nth-child(3) {
      top: 12px; }
    .navigation__toggle span:nth-child(4) {
      top: 24px; }
  .navigation__toggle.open span:nth-child(1), .navigation__toggle.open span:nth-child(4) {
    top: 18px;
    width: 0%;
    left: 50%; }
  .navigation__toggle.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
